import React, { Component } from 'react';
import { BrowserRouter, Route, Switch, Redirect } from 'react-router-dom';

import Home from './components/Home';
import AboutMe from './components/AboutMe';
import Education from './components/Education';
import Experience from './components/Experience';
import Skills from './components/Skills';
import Portfolio from './components/Portfolio';
import Connect from './components/Connect';
import NotFoundPage from './components/NotFoundPage';
import './App.css';

class App extends Component{
  render()  {
    return (
      <BrowserRouter>
        <div>
          <Switch>
            <Route exact path={process.env.PUBLIC_URL + '/'} component={Home} />
            <Route exact path={process.env.PUBLIC_URL + '/AboutMe'} component={AboutMe} />
            <Route exact path={process.env.PUBLIC_URL + '/Education'} component={Education} />
            <Route exact path={process.env.PUBLIC_URL + '/Experience'} component={Experience} />
            <Route exact path={process.env.PUBLIC_URL + '/Skills'} component={Skills} />
            <Route exact path={process.env.PUBLIC_URL + '/Portfolio'} component={Portfolio} />
            <Route exact path={process.env.PUBLIC_URL + '/Connect'} component={Connect} />
            <Route path={process.env.PUBLIC_URL + '/404'} component={NotFoundPage} />
            <Redirect to={process.env.PUBLIC_URL + '/404'} />
          </Switch>
        </div>
      </BrowserRouter>
    );
  }
}

export default App;